import Vue from 'vue';
import i18n from '../../../i18n';
import form from '../../../common/scripts/post-form';
export default Vue.extend({
    i18n: i18n('desktop/views/components/post-form.vue'),
    watch: {
        text() {
            this.doPreview();
        },
        files() {
            this.doPreview();
        },
        visibility() {
            this.doPreview();
        },
        localOnly() {
            this.doPreview();
        },
    },
    mixins: [
        form({
            onSuccess: self => {
                self.$notify(self.renote
                    ? self.$t('reposted')
                    : self.reply
                        ? self.$t('replied')
                        : self.$t('posted'));
            },
            onFailure: self => {
                self.$notify(self.renote
                    ? self.$t('renote-failed')
                    : self.reply
                        ? self.$t('reply-failed')
                        : self.$t('note-failed'));
            }
        }),
    ],
});
