import Vue from 'vue';
import i18n from '../../../i18n';
import anime from 'animejs';
import XVisibilityIcon from './visibility-icon.vue';
export default Vue.extend({
    i18n: i18n('common/views/components/visibility-chooser.vue'),
    components: {
        XVisibilityIcon,
    },
    props: {
        source: {
            required: true
        },
        currentVisibility: {
            type: String,
            required: false
        }
    },
    data() {
        return {
            v: this.$store.state.settings.rememberNoteVisibility ? (this.$store.state.device.visibility || this.$store.state.settings.defaultNoteVisibility) : (this.currentVisibility || this.$store.state.settings.defaultNoteVisibility)
        };
    },
    mounted() {
        this.$nextTick(() => {
            const popover = this.$refs.popover;
            const rect = this.source.getBoundingClientRect();
            const width = popover.offsetWidth;
            const height = popover.offsetHeight;
            let left;
            let top;
            if (this.$root.isMobile) {
                const x = rect.left + window.pageXOffset + (this.source.offsetWidth / 2);
                const y = rect.top + window.pageYOffset + (this.source.offsetHeight / 2);
                left = (x - (width / 2));
                top = (y - (height / 2));
            }
            else {
                const x = rect.left + window.pageXOffset + (this.source.offsetWidth / 2);
                const y = rect.top + window.pageYOffset + this.source.offsetHeight;
                left = (x - (width / 2));
                top = y;
            }
            if (left + width > window.innerWidth) {
                left = window.innerWidth - width;
            }
            popover.style.left = left + 'px';
            popover.style.top = top + 'px';
            anime({
                targets: this.$refs.backdrop,
                opacity: 1,
                duration: 100,
                easing: 'linear'
            });
            anime({
                targets: this.$refs.popover,
                opacity: 1,
                scale: [0.5, 1],
                duration: 500
            });
        });
    },
    methods: {
        choose(visibility) {
            if (this.$store.state.settings.rememberNoteVisibility) {
                this.$store.commit('device/setVisibility', visibility);
            }
            this.$emit('chosen', visibility);
            this.destroyDom();
        },
        close() {
            this.$refs.backdrop.style.pointerEvents = 'none';
            anime({
                targets: this.$refs.backdrop,
                opacity: 0,
                duration: 200,
                easing: 'linear'
            });
            this.$refs.popover.style.pointerEvents = 'none';
            anime({
                targets: this.$refs.popover,
                opacity: 0,
                scale: 0.5,
                duration: 200,
                easing: 'easeInBack',
                complete: () => this.destroyDom()
            });
        }
    }
});
